
:-webkit-full-screen video {
  width: 100%;
  height: 100%;
}
:-moz-full-screen video{
  width: 100%;
  height: 100%;
}
.voice-box .bos-button--primary{
  background:#e9efed;
  border-color:#e9efed;
  color:#000;
}
.bos-button.play-video{ margin-left:-3px;margin-top:20px;
}
.voice-box>img{
  width:60%;
  height:4rem;
  margin-bottom:1rem;
  border:solid 2px #fff;
  display:block;
  margin-left:32px;
}
#videoBoxPlot{
  background:#fff;
  position:absolute;
  top:-5px;
  left:-3.5rem;
  z-index:20004;
  overflow:hidden;
}
#videoBoxPlot #videoCenterPlot{margin-left:25%;
}
#videoBoxPlot .bos-icon-close{
  position:absolute;
  top:20px;
  left:20px;
}



