
.icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.addinput{
  width: 85%;
  margin-top: 0.6rem;
  margin-left: 0.5rem;
  padding: 0.1rem 0.5rem;
}
.addborder{
  width: 85%;
  height: 0.11rem;
  margin-left: 0.5rem;
  padding: 0.1rem 0.2rem;
  border-left: 1px solid #a7a7a7;
  border-right: 1px solid #a7a7a7;
  border-bottom: 1px solid #a7a7a7;
}
.icon-add{
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  background: url("../../../../static/images/im_group_add.png") no-repeat;
  background-size: 100%;
  margin-right: 0.2rem;
  position: relative;
  top: .55rem;
  left:.6rem;
}
.grouplist{ padding:0 2%;
}
.grouplist li{
  height: 3.2rem;
  line-height: 3.2rem;
  border-bottom: 1px solid #e9e9e9;
}
.grouplist li span{
  padding: 0 0.7rem;
  display: inline-block;
  width: 90%;
  font-size: 1.05rem;
}
.icon-delete{
  display: inline-block;
  width: 1.3rem;
  height: 1.3rem;
  background: url("../../../../static/images/im_delete_group.png") no-repeat;
  background-size: 100%;
  margin-right: 0.2rem;
  position: relative;
  top: .5rem;
  left: .6rem;
}



