
.mePro_content>ul>li>i{
   display: inline-block;
   width: 1.7rem;
   height: 1.7rem;
   background: url("../../../static/plotimg/icon_list_indicator.png") no-repeat;
   background-size: 70%;
   position: absolute;
   top: 0.8rem;
   right: 0;
}
.mePro_content{
  margin-top:3rem;
}
.mePro_content>ul{
  padding:.1rem 1rem;
}
.mePro_content>ul>li{
  line-height: 3rem;
  position: relative;
  border-bottom:solid 1px #eee;
}
.bos-switch{
  display: inline-block;
  float: right;
  height: 2.2rem;
  top:.5rem;
}
.mePro_content .bos-switch__core{
  border-color: #d1d1d1;
  background-color: #d1d1d1;
}
.mePro_content .bos-switch__input:checked+.bos-switch__core{
  border-color: #9ee75a;
  background-color: #9ee75a;
}



