
.tab_content .hd .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.tab_content .hd span{
  position: absolute;
  left: 0;
}
.tab_menu .selected{color:#6ea003;
}
.tab_menu .selected i{background-position:0 -2.06rem;
}
.tab_menu .selected .bottom_two {top:.4rem
}
.tab_menu > .footer{
  position: fixed;
  bottom:0;
  width:100%;
  line-height:3rem;
  border-top:solid 1px #eee;
  border-bottom:solid 1px #eee;
  background:#fff;
  z-index:1001;
}
.tab_menu > .footer > ul > li{
  width:33.33%;
  font-size: 1.3rem;
  text-align: center;
  border-right:1px solid #eee;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float:left;
}
.tab_menu > .footer > ul > li:nth-last-child(1) {border-right:none;
}
.c ul{
  background-color: #fff;
  list-style: none;
  padding: 0 0.9rem;
  margin-top: 0;
}
.c ul li{
  height: 3rem;
  line-height: 3rem;
  border-bottom: 1px solid #e9e9e9;
}
.c ul li:nth-last-of-type(1){ border-bottom:none;
}
.c ul li i{
  position: relative;
  top: 0.55rem;
  padding-right: 0.5rem;
}
.c ul li span{
  font-size: 1.1rem;
}
.c .hd{
  text-align: center;
  position: absolute;
  top: 0;
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  background: url("../../../../static/images/im_header_bg.png") no-repeat;
  background-size: 100% 3rem;
  font-size: 1.2rem;
  color: #fff;
}
.c .ifm{
  border-top: 5px solid #ededed;
  border-bottom: 8px solid #cfd3d6;
  background-color: #fff;
  position: absolute;
  top: 3rem;
  width: 100%;
  padding:6px 0;
}
.c .ifm img{
  border-radius: 50%;
  height: 4rem;
  width: 4rem;
  margin-left: 1rem;
  margin-right: .6rem;
}
.c .ifm span{
  position:relative;
  bottom: 1.4rem;
}
.c .list{
  width: 100%;
  overflow: hidden;
  position: absolute;
  top: 8.45rem;
  bottom: 2.75rem;
}
.c .list-inner{
  height: 96%;
  width: 99%;
  overflow-y: auto;
  overflow-x: hidden;
}
.list-inner li i:nth-of-type(2){float:right;margin-right:10px;margin-top:8px;
}
.c .icon-online{
  display: inline-block;
  width: 1.4rem;
  height: 1.4rem;
  background: url("../../../../static/images/im_icon_online.png") no-repeat;
  background-size: 100%;
  margin-right: 0.2rem;
  position: relative;
  top: .4rem;
  left:.4rem;
}
.c .icon-offline{
  display: inline-block;
  width: 1.4rem;
  height: 1.4rem;
  background: url("../../../../static/images/im_icon_offline.png") no-repeat;
  background-size: 100%;
  margin-right: 0.2rem;
  position: relative;
  top: .4rem;
  left:.4rem;
}
.c .list-inner li i{
  width: 2rem;
  height: 2rem;
  display: inline-block;
}
.c .icon-2{
  background: url("../../../../static/images/im_comm_ap_msgsetting.png") no-repeat;
  background-size: 2rem;
}
.c .icon-3{
  background: url("../../../../static/images/im_comm_ap_audio.png") no-repeat;
  background-size: 2rem;
}
.c .icon-4{
  background: url("../../../../static/images/im_comm_ap_msgmodle.png") no-repeat;
  background-size: 2rem;
}
.c .icon-5{
  background: url("../../../../static/images/im_cellphon_icon.png") no-repeat;
  background-size: 2rem;
}
.c .icon-6{
  background: url("../../../../static/images/im_chat_record_icon.png") no-repeat;
  background-size: 2rem;
}
.c .icon-7{
  background: url("../../../../static/images/im_comm_ap_about.png") no-repeat;
  background-size:2rem;
}
.c .icon-8{
  background: url("../../../../static/images/im_comm_ap_msgmodle.png") no-repeat;
  background-size: 2rem;
}
.bottom_one,.bottom_two,.bottom_three{
  width:2rem;
  height:2rem;
  display:inline-block;
  position:relative;
  top:.5rem;
  right:.5rem;
  background: url("../../../../static/images/chat.png") no-repeat 0 0;
  background-size: 100%;
}
.bottom_two{
  background: url("../../../../static/images/contact.png") no-repeat 0 0;
  background-size: 100%;
}
.bottom_three{
  background: url("../../../../static/images/me.png") no-repeat 0 0;
  background-size: 100%;
}



