.paint-header .top .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.paint-header .top .selection{
  position: absolute;
  right: 0.8rem;
}
.paint-header .top .selection label{
  margin: 0 0.2rem;
}
.handpaint_menu > .footer{
  position: fixed;
  bottom:0;
  width:100%;
  line-height:2.5rem;
  background: rgba(0,0,0,.07);
  z-index:1001;
}
.handpaint_menu > .footer > ul > li{
  float:left;
  width:17%;
  margin: 1.5%;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #fafbf6;
  color: #4a8bcb;
  font-size: 1rem;
}
.take-picture{
  border-radius: 3px;
  text-decoration: none;
  color: #fff;
}
.photograph{
  width: 2.6rem;
  position: absolute;
  overflow: hidden;
  right: 5.2rem;
  top: .6rem;
  opacity: 0;
}
.gallery{
  width: 2.6rem;
  position: absolute;
  overflow: hidden;
  right: 2.6rem;
  top: .6rem;
  opacity: 0;
}
.hidden {
  display: none;
}
.era {
  width: 20px;
  height: 20px;
  background-color: rosybrown;
  position: absolute;
}
#cuxi{
  width: 6rem;
  position: relative;
  top: .4rem;
}
.figure{
  position: fixed;
  top: 9.6rem;
  left: 15.5rem;
  width: 90px;
  background: #fff;
  border: 1px solid #ddd;
  visibility: hidden;
  border-radius: 2px;
  margin-top: 2px;
  padding: 10px;
  padding-bottom: 5px;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,.15);
          box-shadow: 0 0 5px rgba(0,0,0,.15);
  opacity: 0;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.hand-paint .figure.open{visibility: visible;opacity: 1;
}



