
.adhoc-ptt .pttHd{
  text-align: center;
  position: absolute;
  top: 0;
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  background: url("../../../../static/images/im_header_bg.png") no-repeat;
  background-size: 100% 3rem;
  font-size: 1.2rem;
  color: #fff;
}
.adhoc-ptt .pttHd span{
  position: absolute;
  left: 0;
}
.adhoc-ptt .pttHd .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.adhoc-ptt{
  width:100%;
  height:100%;
  overflow:hidden;
}
.adhoc-ptt>.pttCon{
  width:160px;
  height:160px;
  background-color:#ccc;
  border-radius:80px;
  position:absolute;
  left:50%;
  top:50%;
  margin-left:-80px;
  margin-top:-80px;
}
.adhoc-ptt>.pttCon:active{ background:#2d8b56;
}
.adhoc-ptt>.pttCon>p{
 text-align:center;
  padding-top:160px;
}



