
.icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.color-list{
  background-color: #f8f8f8;
  height: 15rem;
  margin-top: 3rem;
  padding: 1rem;
  position: relative;
}
input[type=radio]{
  position: absolute;
  top: 0.7rem;
  margin-left: 0.7rem;
  z-index: 1001;
}
.color-1, .color-2, .color-3, .color-4, .color-5, .color-6{
  float: left;
  width: 4.5rem;
  height: 6rem;
  margin: 0.5rem;
  position: relative;
  background-color: #feffef;
}
.color-1{
  background-color: #feffef;
}
.color-2{
  background-color: #f5deb2;
}
.color-3{
  background-color: #efe68b;
}
.color-4{
  background-color: #afe0e5;
}
.color-5{
  background-color: #2d8b56;
}
.color-6{
  background-color: #d1b48a;
}
.icon-nochoose{
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  background: url("../../../../static/images/im_radiobutton_multi_nochoose.png") no-repeat;
  background-size: 100%;
  margin-right: 0.2rem;
  position: absolute;
  top: 0.1rem;
  right: -0.1rem;
}
.icon-choose{
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  background: url("../../../../static/images/im_radiobutton_multi_choose.png") no-repeat;
  background-size: 100%;
  margin-right: 0.2rem;
  position: absolute;
  top: 0.1rem;
  right: -0.1rem;
}
.chatbg-foot{
  width: 100%;
  padding-top: 0.2rem;
  padding-left: 13%;
  background: #ebebeb;
  position: fixed;
  bottom: 0;
}
.chatbg-foot .btn{
  width: 27%;
  height: 3rem;
  font-size: 1.2rem;
  margin: 0.4rem 5%;
}



