
.choosegroup-group{
  position: relative;
}
.choosegroup-box{
  margin-top: 3rem;
  margin-bottom: 3.5rem;
}
.choosegroup-list{
  padding-left: 4%;
  line-height: 3.2rem;
  border-bottom: 1px solid #e9e9e9;
}
.choosegroup-list span{
  margin-left: 0.9rem;
}
.choosegroup-list label{
  position: relative;
  right: 1rem;
  text-align: center;
  float: right;
  color: #999;
}
.icon-nochoose{
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  background: url("../../../static/images/im_radiobutton_multi_nochoose.png") no-repeat;
  background-size: 100%;
  margin-right: 0.2rem;
  position: absolute;
  top: .85rem;
  left: .6rem;
}
.icon-choose{
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  background: url("../../../static/images/im_radiobutton_multi_choose.png") no-repeat;
  background-size: 100%;
  margin-right: 0.2rem;
  position: absolute;
  top: .85rem;
  left: .6rem;
}
.icon-righttri{
  display: inline-block;
  width: 1rem;
  height: 1rem;
  background: url("../../../static/images/im_group_indicator_unexpanded01.png") no-repeat;
  background-size: 100%;
  position: relative;
  top: 1.1rem;
  margin-right: 1rem;
  float: right;
}
.icon-downtri{
  display: inline-block;
  width: 1rem;
  height: 1rem;
  background: url("../../../static/images/im_group_indicator_expanded01.png") no-repeat;
  background-size: 100%;
  position: relative;
  top: 1.1rem;
  margin-right: 1rem;
  float: right;
}
.icon-nochoose-children,.icon-choose-children{
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  margin-right: 0.2rem;
  position: relative;
  top: 1.15rem;
}
.icon-nochoose-children{
  background: url("../../../static/images/im_radiobutton_multi_nochoose.png") no-repeat;
  background-size: 100%;
}
.icon-choose-children{
  background: url("../../../static/images/im_radiobutton_multi_choose.png") no-repeat;
  background-size: 100%;
}
.choose-group-ull{
  background:#fff;
}
.choose-group-ull li{
  border-top:solid 1px #eee;
  height:3.7rem;
  line-height: 1.5rem;
  padding:0 1.2rem;
}
.choose-group-ull li a{ width:90%;display:inline-block;
}
.choose-group-ull li a>img{width:8%;height:8%;margin-top:.15rem;float:left;border-radius: 50%
}
.choose-group-ull li a>span { float:left;padding-left:1rem;
}
.choose-group-ull li a>label { float:left;padding-left:1rem;color: blue;font-size: 0.9rem
}



