
.icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: absolute;
  top: 0.8rem;
  left: 0.5rem;
}
.content{
  overflow-y: auto;
}
.titleline{
  padding: 0 1.2rem 0 6px;
  height: 3rem;
  line-height: 3rem;
  background-color: #e4e4e4;
}
.titleline img{
  width:20px;
  height: 20px;
  margin-bottom: -6px;
}
.right_info img{
  width:20px;
  height: 20px;
  margin-bottom: -6px;
}
.border-B {
  border-bottom:1px solid gainsboro;
}
.infoline {
  width:94%;
  margin: 0 auto;
  height: 3rem;
  line-height: 3rem;
}
.infoline .avatar-Info{
  width:30px;
  height: 30px;
  border-radius: 50%;
  position: relative;
  top:0.25rem;
}
 /*img{
   width:25px;
   height: 25px;
   margin-bottom: -6px;
}*/
.left_info {
  float: left;
}
.right_info {
  float: right;
}
#information .right_info span{
  font-size: 0.95rem;
}
.condition-info{
  font-size: 0.95rem;
  position: relative;
  top: 0.1rem;
  width: 50px;
  display: inline-block;
  text-align: center;
}



