
.emergency .top .icon-back{
   display: inline-block;
   width: 1.7rem;
   height: 1.7rem;
   background: url("../../../static/images/im_header_back.png") no-repeat;
   background-size: 40%;
   position: relative;
   top: 0.77rem;
   left: 0.5rem;
}
.emergency .top span{
   position: absolute;
   left: 0;
}
.info {
   width: 94%;
   margin: 4rem auto;
   height: 3rem;
   line-height: 3rem;
}
#friends{
   border: 1px solid #e4e4e4;
   background-color: #fff;
   width:85%;
   line-height: 3rem;
   height: 3rem;
   margin-left: 10px;
}
#friends:active{
  background-color: #cfd3d6;
}



