
.m-colorPicker{
  position: relative; text-align: left; font-size: 14px; display: inline-block;top: .15rem;left: .15rem;
}
.m-colorPicker ul,li,ol{ list-style: none; margin: 0; padding: 0;
}
.m-colorPicker input{ display: none;
}
.m-colorPicker .colorBtn{ width: 15px; height: 15px;
}
.m-colorPicker .colorBtn.disabled{ cursor: no-drop;
}
.m-colorPicker .box{
  position: fixed;top: .2rem;left: 0.3rem;; width: 190px; background: #fff; border: 1px solid #ddd; visibility: hidden; border-radius: 2px; margin-top: 2px; padding: 10px; padding-bottom: 5px; -webkit-box-shadow: 0 0 5px rgba(0,0,0,.15); box-shadow: 0 0 5px rgba(0,0,0,.15); opacity: 0; -webkit-transition: all .3s ease; transition: all .3s ease;
}
.m-colorPicker .box h3{ margin: 0; font-size: 14px; font-weight: normal; margin-top: 10px; margin-bottom: 5px; line-height: 1;
}
.m-colorPicker .box.open{ visibility: visible; opacity: 1;
}
.m-colorPicker .hd{
  overflow: hidden; line-height: 29px;
}
.m-colorPicker .hd .colorView{ width: 100px; height: 30px; float: left; -webkit-transition: background-color .3s ease; transition: background-color .3s ease;
}
.m-colorPicker .hd .defaultColor{ width: 80px; float: right; text-align: center; border: 1px solid #ddd; cursor: pointer;
}
.m-colorPicker .tColor li{ width: 15px; height: 15px; display: inline-block; margin: 0 2px; -webkit-transition: all .3s ease; transition: all .3s ease;
}
.m-colorPicker .tColor li:hover{ -webkit-box-shadow: 0 0 5px rgba(0,0,0,.4); box-shadow: 0 0 5px rgba(0,0,0,.4); -webkit-transform: scale(1.3); transform: scale(1.3);
}
.m-colorPicker .bColor li{
  width: 15px; display: inline-block; margin: 0 2px;
}
.m-colorPicker .bColor li li{ display: block; width: 15px; height: 15px; -webkit-transition: all .3s ease; transition: all .3s ease; margin: 0;
}
.m-colorPicker .bColor li li:hover{ -webkit-box-shadow: 0 0 5px rgba(0,0,0,.4); box-shadow: 0 0 5px rgba(0,0,0,.4); -webkit-transform: scale(1.3); transform: scale(1.3);
}



