
.tm{
  padding: 0;
  margin:3rem 0 0;
  font-size: 1.2rem;
  background: #fff;
}
.tm .hd{
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  background: url("../../../../static/images/im_header_bg.png") no-repeat;
  background-size: 100% 3rem;
  color: #fff;
}
.tm .hd h1{
  font-weight: normal;
  margin: 0 4rem 0 0;
  font-size: 1.2rem;
}
.tm .hd span{
  float: left;
  font-size: 1rem;
}
.tm .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.tm .leadin{
  background-color: #ebebeb;
  padding: 2% 0;
}
.tm .leadin-btn{
  width: 96%;
  margin: 0 2%;
  background-color: #e9625f;
  color: #fff;
  text-align: center;
  height: 3.5rem;
  line-height: 3.5rem;
  border-radius: 5px;
}



