
.icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.partner{
  border-top: 5px solid #ededed;
  border-bottom: 8px solid #cfd3d6;
  background-color: #ffffff;
  position: absolute;
  top: 3rem;
  width: 100%;
  height:4.5rem;
}
.partner img{
  border-radius: 50%;
  height: 4rem;
  width: 4rem;
  margin: .25rem 1rem 0 .6rem;
}
.partner span{
  position:relative;
  bottom: 1.6rem;
}
.chathistory{
  margin-top: 5.3rem;
  line-height: 2.5rem;
  border-bottom: 8px solid #cfd3d6;
  background-color: #fff;
}
.icon-record{
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background: url("../../../../static/images/im_chat_record_icon.png") no-repeat;
  background-size: 100%;
  float: left;
  position: relative;
  top: 0.25rem;
  margin-left: 0.7rem;
  margin-right: 0.6rem;
}
.chatbackground{
  line-height: 2.5rem;
  border-bottom: 8px solid #cfd3d6;
  background-color: #fff;
}
.icon-chatbg{
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background: url("../../../../static/images/im_chat_bg_setting_icon.png") no-repeat;
  background-size: 100%;
  float: left;
  position: relative;
  top: 0.25rem;
  margin-left: 0.7rem;
  margin-right: 0.6rem;
}
.deletefriend{
  width: 96%;
  margin: auto 2%;
  line-height: 3rem;
  text-align: center;
  border-radius: 3px;
  background-color: #e9625f;
  margin-top: 3.3rem;
  margin-bottom: 0.5rem;
  color: #fff;
}



