
.tools .top .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.tools .top span{
  position: absolute;
  left: 0;
}
.content{
  margin-top: 0;
}
.border-B {
  border-bottom:1px solid gainsboro;
}
.tools-line{
  width: 94%;
  margin: 0 auto;
  height: 3rem;
  line-height: 3rem;
}
.left_info {
  float: left;
}
.right_info {
  float: right;
}
.clearfix {
  overfloat: auto;
}
.cell{
  text-align: center;
}
.tools-arrow-right{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../static/plotimg/icon_list_indicator.png") no-repeat;
  background-size: 70%;
  position: relative;
  top: 0.9rem;
  right: -0.5rem;
}



