.border-B {
  border-bottom:1px solid gainsboro;
}
.trendLine{
  width: 94%;
  margin: 0 auto;
  height: 3rem;
  line-height: 3rem;
}
.left_info {
  float: left;
}
.right_info {
  float: right;
}
.right_info .bos-button span{
  font-size: 0.9rem;
  position: relative;
  top:0.1rem;
}
.clearfix {
  overflow: auto;
}
.shareTrand-arrow-right{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../static/plotimg/icon_list_indicator.png") no-repeat;
  background-size: 70%;
  position: relative;
  top: 0.9rem;
  right: -0.5rem;
}



