
.chat-many-list.chat-list li p+div{
  background:#fff;
  border:solid 1px #eee;
  border-radius:4px;
  margin-top:10px;
}
.chat-list ul{
  width:100%;
}
.chat-list .other{
  width:100%;
  border-radius: 3px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  margin: 0.5rem 0 0.3rem 0;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
}
.chat-list .with{
  width:100%;
  background: #fafbf6;
  border-radius: 3.5px;
  position: relative;
  top:0.2rem;
}
.chat-list .with p{
  width:98%;
  display: inline-block;
  margin: 0.5%;
  padding: 0.5%;
  text-align: left;
  background: #efefef;
  border-radius: 3.5px;
}
.chat-list .with label:nth-of-type(1){
  font-size: 0.95rem;
  color: #999;
}
.chat-list .with label:nth-of-type(2){
  font-size: 1rem;
  color: #999;
}
.chat-list .whatsay-text a{background: rgb(230, 230, 10);
}
.chat-list #ullMany .mysay>.whatsay>i,.chat-list #ullMany .mysay>.photoshow>i{
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  background: url("../../../../static/images/im_msg_state_fail_resend.png") no-repeat;
  background-size: 100%;
  position: absolute;
  top:0.8rem;
  left: -1.6rem;
}
.chat-list #ullMany .mysay>.photoshow>i{
  position: relative;
  left: -1.6rem;
  top: -3em;
}



