
.logins{
  width: 100%;
  overflow: hidden;
  background-image: url("../../../static/images/im_login_bg.png");
  z-index: -1;
}
.login-box{
  width: 56%;
  margin:4.0rem 22% auto;
  background-color: #fff;
  border-radius: 4px;
  -webkit-box-shadow: 0 0 10px #000;
          box-shadow: 0 0 10px #000;
  padding-bottom: 1%;
  position: relative;
}
.login-avatar img:nth-of-type(1){
  width: 8rem;
  height:8rem;
  margin: auto 31.5%;
  position: absolute;
  top: -4rem;
}
.login-avatar img:nth-of-type(2){
  width: 12%;
  margin: auto 44%;
  position: absolute;
  top: -1.4rem;
  left: 0.16rem;
  height:3rem;
}
@media screen and (min-width:700px){
.login-form{
    padding-top: 15%;
    position: relative;
}
}
@media screen and (max-width:699px){
.login-form{
    padding-top: 10%;
    position: relative;
}
}
.account{
  float: left;
  width: 84%;
  margin-left: 1.2rem;
  margin-top: 1.2rem;
  border-bottom: 1px solid #eee;
  position: relative;
}
.account-list{
  position: absolute;
  left: 3.2rem;
  top: 5.2rem;
  width: 84%;
  background-color: #eee;
  z-index: 1004;
}
.account-list li{
  display: inline-block;
  width: 100%;
  border-bottom: 1px solid #eee;
  line-height: 2.2rem;
  position: relative;
}
.icon-deleteuser{
  display: inline-block;
  width: 1rem;
  height: 1rem;
  background: url("../../../static/images/im_login_icon_user_delete.png") no-repeat;
  background-size: 70%;
  position: absolute;
  right: 0.6rem;
  top:0.77rem;
}
.account input{
  line-height: 2rem;
}
.password{
  float: left;
  width: 84%;
  margin-left: 1.2rem;
  margin-top: 1.2rem;
  border-bottom: 1px solid #eee;
}
.password input{
  line-height: 2rem;
}
.icon-user{
  float: left;
  display: inline-block;
  width: 2rem;
  height: 2.4rem;
  background: url("../../../static/images/im_login_icon_user_normal.png") no-repeat;
  background-size: 70%;
  position: relative;
  top: 1.6rem;
  left: 1.1rem;
}
.icon-pw{
  float: left;
  display: inline-block;
  width: 2rem;
  height: 2.4rem;
  background: url("../../../static/images/im_login_icon_pw_normal.png") no-repeat;
  background-size: 70%;
  position: relative;
  top: 1.6rem;
  left: 1.1rem;
}
.icon-pull{
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background: url("../../../static/images/im_login_icon_user_pull.jpg") no-repeat;
  background-size: 70%;
  position: absolute;
  right: 0.1rem;
  bottom: 9.9rem;
}
.login-form p{
  clear: both;
  display: inline-block;
  padding: 0.8rem 1.2rem;
  font-weight: bold;
}
.login-form span:nth-of-type(1){
  margin-left: 1.4rem;
  margin-right: 6rem;
  position: relative;
}
.login-form span:nth-of-type(2){
  margin-left: 1.4rem;
  position: relative;
}
.icon-checkoff{
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background: url("../../../static/images/im_login_check_off.png") no-repeat;
  background-size: 50%;
  position: absolute;
  top: 0.05rem;
  left: -1.2rem;
}
.icon-checkon{
  display: inline-block;
  width: 2rem;
  height: 2rem;
  background: url("../../../static/images/im_login_check_on.png") no-repeat;
  background-size: 50%;
  position: absolute;
  top: 0.05rem;
  left: -1.2rem;
}
.login-btn{
  text-align: center;
}
.icon-loginbtn{
  display: inline-block;
  width: 90%;
  height: 3rem;
  margin-top: 1rem;
  background: url("../../../static/images/im_login_btn_normal.png") no-repeat;
  background-size: 100%;
}
.login-btn label{
  font-weight: bold;
  font-style: normal;
  display: inline-block;
  width: 24%;
  text-align: center;
  margin: auto 38%;
  padding-top: 4%;
}



