
.group-info .list-info .outGroup{
  padding: 2px;
  border: none;
  background-color: #ff2638;
  width: 95%;
  height: 2.6rem;
  border-radius: 10px;
  color: white;
  margin-left: 2.5%;
  margin-top: .5%;
}
.group-info .top{
  position: relative;
}
.group-info .top .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.group-info .top span{
  position: absolute;
  left: 0;
}
.group-name{
  border-top: 5px solid #ededed;
  border-bottom: 8px solid #cfd3d6;
  background-color: #ffffff;
  position: absolute;
  top: 3rem;
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  font-size: 20px;
}
.group-info .list-content{
  width: 100%;
  margin: 0 auto;
  overflow: scroll;
  position: absolute;
  top: 7rem;
}
.group-info ul li{
  height: 3rem;
  line-height: 3rem;
  font-size: 0.9rem;
  border-bottom: 1px solid #e9e9e9;
}
.group-info ul li span{
  font-size: .9rem;
}
.group-info ul li i{
  position: relative;
  top: .55rem;
  padding-right: .5rem;
  margin-left: .5rem;
}
.list-info{
  width: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}
.list-info .groupInfo{
  color: grey;
  margin-left: 1.5rem;
}
.list-info li i:nth-of-type(2){float:right;margin-right:10px;margin-top:8px;
}
#groupPeople{
  height: 8.4rem;
  border-bottom: 5px solid #cfd3d6 ;
}
#groupPeople .groupPeople{
  margin-left: 1.5rem;
  overflow-x: scroll;
  overflow-y: hidden;
  height: 5rem;
}
#groupPeople .groupPeople .groupPerson{
  float: left;
  margin: 3px 7px;
}
.groupPerson p{
  width: 3rem;
  text-align: center;
  font-size: 11px;
  display: block;
  height: 1.5rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  line-height: 1.5rem;
}
.groupPerson .personImg{
  height: 3rem;
}
.groupPerson .personImg img{
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
}
.group-info .icon-2,.group-info .icon-3,.group-info .icon-4,.group-info .icon-5,.group-info .icon-6,.group-info .icon-7{
  width: 1.5rem;
  height: 1.5rem;
  display: inline-block;
  position: relative;
  top: .37rem;
}
.group-info .icon-2{
  background: url("../../../../static/images/im_pub_goup_notice.png") no-repeat;
  background-size: 1.5rem;
}
.group-info .icon-3{
  background: url("../../../../static/images/im_pub_goup_notice.png") no-repeat;
  background-size: 1.5rem;
}
.group-info .icon-4{
  background: url("../../../../static/images/im_pub_goup_card.png") no-repeat;
  background-size: 1.5rem;
}
.group-info .icon-5{
  background: url("../../../../static/images/im_pub_goup_mem.png") no-repeat;
  background-size: 1.5rem;
}
.group-info .icon-6{
  background: url("../../../../static/images/im_chat_record_icon.png") no-repeat;
  background-size: 1.5rem;
}
.group-info .icon-7{
  background: url("../../../../static/images/im_chat_bg_setting_icon.png") no-repeat;
  background-size: 1.5rem;
}



