








































































































































/*会话页面的样式编写*/
.guide-content .top .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.guide-content .top span:nth-of-type(1){
  position: absolute;
  left: 0;
}
#distance{
  position: absolute;
  top: 3rem;
  font-size: 0.95rem;
  margin: 1%;
}
.guide-near{
  position: absolute;
  right:.2rem;
  bottom:.3rem;
  display: inline-block;
  z-index: 1001;
}
.guide-near>p:nth-of-type(1){background:url("../../../static/plotimg/map_zoomin_sel.png") no-repeat; background-size:100%;
}
.guide-near>p:nth-of-type(2){background:url("../../../static/plotimg/map_zoomout_sel.png") no-repeat; background-size:100%;
}
.guide-near>p{
  width:40px;
  height:40px;
}
.guide-fix{
  width:40px;
  height:40px;
  position:absolute;
  bottom:.3rem;
  left:.2rem;
  background:url("../../../static/plotimg/map_location.png") no-repeat;
  background-size:100%;
  z-index: 1001;
}
.guide-free{
  color:#fff;
  font-size:.8rem;
  border-radius:4px;
  position:absolute;
  bottom:.3rem;
  right:3rem;
  padding:.3rem .5rem;
  background:rgba(0,0,0,0.5);
  z-index: 1001;
}
.guide-rate{
  font-size: 0.9rem;
  position:absolute;
  bottom:.57rem;
  left: 2.9rem;
  z-index: 1001;
}
.guide-rate-border{
  height: 0.1rem;
  width: 3rem;
  border: 1px solid #000;
  border-top: 0;
  position: absolute;
  bottom: .47rem;
  left: 2.8rem;
  z-index: 1001;
}



