
.infoLine{
  width:94%;
  margin: 0 auto;
  height: 3rem;
  line-height: 3rem;
}
.left_info {
  float: left;
}
.right_info {
  float: right;
}
.message-reminder .right_info span{
  font-size:0.95rem;
}
.clearfix {
  overflow: auto;
}
.titleLine{
  height: 3rem;
  line-height: 3rem;
  background-color: #e4e4e4;
  padding-left: 10px;
}
.border-B {
  border-bottom:1px solid gainsboro;
}
.bos-switch{
  display: inline-block;
  float: right;
  height: 2.2rem;
  top:.5rem;
}
.message-reminder .bos-switch__core{
  border-color: #d1d1d1;
  background-color: #d1d1d1;
}
.message-reminder .bos-switch__input:checked+.bos-switch__core{
  border-color: #9ee75a;
  background-color: #9ee75a;
}



