
.dial-top>p{
  text-align: center;
}
.dial-top>p:nth-of-type(2),.dial-top>p:nth-of-type(3){
  margin: .5rem;
  font-size: 1.1rem;
}
.dial-top>p>img{
  width: 13%;
  height: 13%;
  border-radius: 50%;
  margin-top: 1rem;
}
.dial-middle>p{
  font-size: 1.3rem;
  text-align: center;
}
.dial-footer .before-answer{
  position: fixed;
  left: 3rem;
  bottom: 2rem;
}
.dial-footer .after-answer{
  display: none;
}
.dial-footer .before-hangup{
  position: fixed;
  right: 3rem;
  bottom: 2rem;
}
.dial-footer .after-hangup{
  position: fixed;
  right: 16.6rem;
  bottom: 2rem;
}



