
.as{
  padding: 0;
  margin: 0;
  font-size: 1.2rem;
  background: #ebebeb;
}
.as ul{
  list-style: none;
  padding: 0 0.9rem;
  margin: 0;
  background: #ffffff;
}
.as ul li{
  height: 4.5rem;
  /*line-height: 3rem;*/
  border-bottom: 1px solid #e9e9e9;
}
.as ul li p{
  margin: 0 0 0.5rem 0;
  padding-top: 0.6rem;
}
.as .hd{
  text-align: center;
  position: fixed;
  top: 0;
  z-index: 1001;
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  background: url("../../../../static/images/im_header_bg.png") no-repeat;
  background-size: 100% 3rem;
  color: #fff;
}
.as .hd h1{
  font-weight: normal;
  margin: 0;
  font-size: 1.2rem;
  margin-right: 4rem;
}
.as .hd span{
  float: left;
  font-size: 1rem;
}
.as .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png")  no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.as .audiolist{
  margin-top: 3rem;
  overflow: auto;
}
.as .radio{
  margin-bottom: 0.4rem;
  margin-left: 0.55rem;
}
.as .ft-as{
  width: 100%;
  padding-top: .8rem;
  padding-left: 7%;
  background: #ebebeb;
  position: fixed;
  bottom: 0;
}
.as .ft-as .btn{
  width: 33%;
  height: 3rem;
  font-size: 1.2rem;
  margin: 0.5rem 5%;
  position: relative;
  bottom: 0.5rem;
}



