
#record-ul > li > .other>.whatsay>.whatsay-text > img{
  width:1.2rem;
  height:1.2rem;
}
.detail_menu > .footer{
  position: fixed;
  bottom:0;
  width:100%;
  line-height:3rem;
  border-top:solid 1px #eee;
  border-bottom:solid 1px #eee;
  background:#fff;
  z-index:1001;
}
.detail_menu > .footer > ul > li{
  width:20%;
  text-align: center;
  border-right:1px solid #eee;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  float:left;
  font-size: 0.85rem;
}
.detail_menu > .footer > ul > li:nth-last-child(1) {border-right:none;
}
.history-head{
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  background: url("../../../../static/images/im_header_bg.png") no-repeat;
  background-size: 100% 3rem;
  font-size: 1rem;
  color: #fff;
  z-index: 1111;
}
.history-head h1{
  font-weight: normal;
  margin: 0;
  font-size: 1.2rem;
  margin-right: 4rem;
}
.history-head span{
  float: left;
  font-size: 1rem;
}
.history-head .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.history-box{
  /*高度需要修改*/
  /*height: 19.7rem;*/
  margin: 3rem 0;
}
.history-list{
  position: relative;
  padding:0 .32rem;
  overflow:auto;
  margin:0 auto;
}
.history-box .other{
  width:100%;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  margin-bottom:0.512rem;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
}
.aaa,.refileImg,.reVoiceImg,.voiceImg,.photoImg,.fileImg,.reMessageImg,.rePhotoImg{
  border-radius: 50%;
  display:block;
  width:2.7493333333rem;
  height:2.7493333333rem;
}
.history-box .whatsay {
  position: relative;
}
.history-box .whatsay-text{
  margin:0.63rem;
  max-width:20.7rem;
  background:#fff;
  padding:0.42rem 0.384rem;
  border:1px solid #d9d9d9;
  border-radius:8px;
  font-size: .94rem;
  color: #333;
  line-height:0.9533333333rem;
  word-break: break-all;
}
.history-box .mysay{
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-orient:horizontal;
  -webkit-box-direction:reverse;
      -ms-flex-direction:row-reverse;
          flex-direction:row-reverse;
}
.history-box .mysay .whatsay-text{
  margin-right:0.6399997rem;
  margin-left:0;
  background:#9fe658;
}



