
.container{
  width:300px;
  height:260px;
  position:relative;
  margin:60px auto 0;
}
.container1{
  position:absolute;
  top:0;
  left:50%;
  margin-left:-150px;
}
.box,.box1{
  position:absolute;
  width:20px;
  height:20px;
  line-height:20px;
  background:#999;
  border-radius:50%;
  z-index:10;
}
.box1{
  color:#999;
  background:#fff;
  font-size:.8rem;
  text-align:center;
}
.dot{
  position:absolute;
  width:10px;
  height:10px;
}
#lineWrap{
  position: absolute;
  left:0;
  top:0;
  overflow:visible;
}



