  top: 100%;
  left:0;
  z-index:10000;
  -webkit-transition: top ease 200ms;
  transition: top ease 200ms;
}
.trans{
  top: 0!important;
}
/*复制*/
#copy,#trans{
  border-radius: 16px;
  background: #000;
  height:22px;
  position: fixed;
  bottom: 3em;
  left: 41%;
  padding:4px 5px;
  display: inline-block;
  z-index: 99;
}
#trans{
  left: 44%;
}
#copy ul li,#trans ul li{
  float: left;
  color: #fff;
  padding: 0 5px;
}
#copy ul li:nth-of-type(2){
  border-right: 1px solid #717171;
  border-left: 1px solid #717171;
}
#trans ul li:nth-of-type(1){
  border-right: 1px solid #717171;
}
#paste{
  z-index: 99;
  position: fixed;
  bottom: 3em;
  left: 41%;
  background: #f0f1ef;
  border-radius: 8px;
  border: 1px solid #4aabc1;
  padding: 5px;
}
.questReply{
