}
.questReply .forOk{
  font-size: 0.8rem;
  border: none;
  color: #2d8b56
}
.sureReply{
  width: 16%;
  margin: 0 auto;
  font-size: 0.8rem;
  background-color: gainsboro;
  border-radius: 5%;
}
#videoBox i{
  position:absolute;
  left:20px;
  top:20px;
}



