
.voice-box .bos-button--primary{
  background:#e9efed;
  border-color:#e9efed;
  color:#000;
}
.voice-box textarea{
  width:90%;
  height:6rem;
  margin-left:.3rem;
  padding:4px;
  line-height:1.4rem;
  font-size:.9rem;
  font-family:"微软雅黑";
  background:#6b7150;
  resize:none;
}
.phrase{
  width:100%;
  height:100%;
  position:fixed;
  top:0;
  left:0;
  background:#fff;
}
.phrase>i{
  float:right;
  margin:5%;
}
.phrase-box{padding-top:10%;text-align:center;
}
.phrase-box .bos-select{width:8rem;
}
.phrase-box>input{
  width:3.4rem;
  text-indent:10px;
  border-bottom:solid 1px #999;
}
.phrase .phrase-btn{text-align:center;padding-top:3rem;
}
.phrase-btn .bos-button--primary {width:20%;
}



