
.top_search .top .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.top_search .top span{
  position: absolute;
  left: 0;
}
.searchTitle{
    width: 100%;
}
.selectType{
    margin: 4.5px 8px;
    width: 20%;
    float: left;
    position: relative;
    top: -0.01rem;
}
#type{
    background-color: #bbb;
    height: 2rem;
    line-height: 2rem;
    text-align: center;
    border-radius:5px;
    padding: 2px 35px;
    font-size: 16px;
    outline: none;
}
.searchD{
    float: left;
    width: 65%;
    margin: 3px 8px;
}
.searchI{
    border: 1px solid #999;
    height: 2rem;
    line-height: 2rem;
    width: 100%;
    border-radius:5px;
    padding-left: .2rem;
}
.sobutton{
    width: 8%;
    float: left;
    height: 2rem;
    line-height: 2rem;
    background-color:#6ec1ff;
    text-align: center;
    margin-top: 3px;
    padding:1px 5px;
    border-radius: 5px;
    color: #fff;
}
.clearfix {
    overfloat: auto;
    clear: both;
}
.searchResult{
    border: 1px solid #cfd3d6;
    width: 100%;
    height: 2rem;
    line-height: 2rem;
}
.searchResult span{
    display: block;
    width: 4rem;
    margin: 2px auto;
}



