.tm{
  padding: 0;
  margin: 0;
  font-size: 1.2rem;
  background: #ffffff;
  margin-top: 3rem;
}
.tm .hd{
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  background-image: url("../../../../static/images/im_header_bg.png");
  background-size: 100% 3rem;
  background-repeat: no-repeat;
  color: #ffffff;
}
.tm .hd h1{
  font-weight: normal;
  margin: 0;
  font-size: 1.2rem;
  margin-right: 4rem;
}
.tm .hd span{
  float: left;
  font-size: 1rem;
}
.tm .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background-image: url("../../../../static/images/im_header_back.png");
  background-size: 40%;
  background-repeat: no-repeat;
  position: relative;
  top: 0.77rem;
  left: 0.5rem;
}
.tm .leadin{
  background-color: #ebebeb;
  padding: 2% 0;
}
.tm .leadin-btn{
  width: 96%;
  margin: 0 2%;
  background-color: #e9625f;
  color: #ffffff;
  text-align: center;
  height: 3.5rem;
  line-height: 3.5rem;
  border-radius: 5px;
}



