
.ms{
  padding: 0;
  margin: 0;
  font-size: 1rem;
  background: #fff;
}
.ms ul{
  list-style: none;
  padding: 0 0.9rem;
}
.ms ul li{
  height: 3rem;
  line-height: 3rem;
  border-bottom: 1px solid #e9e9e9;
}
.ms .hd{
  text-align: center;
  /*position: absolute;*/
  /*top: 0;*/
  width: 100%;
  height: 3rem;
  line-height: 3rem;
  background: url("../../../../static/images/im_header_bg.png") no-repeat;
  background-size: 100% 3rem;
  color: #fff;
}
.ms .hd h1{
  font-weight: normal;
  margin: 0 4rem 0 0;
  font-size: 1.2rem;
}
.ms .hd span{
  float: left;
}
.ms .icon-back{
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
  background: url("../../../../static/images/im_header_back.png") no-repeat;
  background-size: 40%;
  position: relative;
  top: .77rem;
  left: .5rem;
}
.ms .fr-m{
  float: right;
  margin-right: 1rem;
}
.fr-m .bos-switch__core{
  border-color: #d1d1d1;
  background-color: #d1d1d1;
}
.fr-m .bos-switch__input:checked+.bos-switch__core{
  border-color: #9ee75a;
  background-color: #9ee75a;
}



